.Techstack{
    display: flex;
    
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: center;
    height: 100vh;
    border-bottom: 5px solid rgb(134, 129, 129, .3);
    color: oldlace;
    background: #000;
    padding-top: 30px;
    
   
   .Techh{
       display: flex;
       flex-direction: row;
       width: 100%;
       margin: auto;
   }
    .IMGS{
        width: 250px;
        height: 250px;
    }
    .Elem{
        display: flex;
        flex-direction: column;
        margin: auto;
        
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        border-radius: 10px;
        box-sizing: border-box;
     
        align-items: center;

    }
}