.navbar{
    

    display:flex;
    height: 100px;
    width: 100%;
    margin-top: 0;
    align-items: center;
    justify-content: center;
    background-color:purple;
    
   
    
    border:1px;
    .navlist{
        display:flex;
        flex-direction: row;
        width: 80%;
        justify-content: space-evenly;
        align-items: center;
        height: 100%;
        list-style: none;
                .navitems{
                    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                    font-family: 'Roboto Slab', serif;
                    font-family: 'Source Sans Pro', sans-serif;
                    cursor: pointer;
                    font-weight: bold;
                    
                    font-size: 2rem;
                    color: white;

                }
                .navitems:hover{
                    color: #f5f5f58c;
                    transform: scale(1.1);

                    }
                    a{
                        text-decoration: none;
                        color: white;}
                    
}
    
    .navimg{
        height: 100%;
        width: 60px;
        margin-left: 10px;
    }
}