.AboutContainer{
    width: 100%;
    background-color:black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    height:100vh;
    font-family: 'Roboto Slab', serif;
    border-bottom: 1px solid white;
.Aboutme{
     font-size:50px;
     color:blueviolet;
    }


.glow {
    font-size: 60px;
    color: #fff;
    text-align: center;
    letter-spacing: .5rem;
        word-spacing: 1rem;

  }
  
 
   

}



    


 .AboutContainer:hover .Aboutme{
        transition: transform 1s;
    }