.Center{
    display:flex;
    flex-direction: row;
  
    align-items: center;
    justify-content: center;
  height:calc(100vh - 100px);
  background-color: rgba(0,0,0,);
    
    width: 100%;
   .Centerimg{
width: 50%;
border-radius:50%;
border:10px solid blue;
animation: imgs 3s ease-in infinite ;
   }
    .left{
        width: 50%;
        margin-left: 10%;
        letter-spacing: .2rem;
        word-spacing: 1rem;
        
       
    //    font-family: 'League Gothic', sans-serif;
    //    font-family: 'Roboto Slab', serif;
    //    font-family: 'Source Sans Pro', sans-serif;
    font-family: fantasy;
       
    
       color: white;
       
       h1{
           font-size:100px;
           font-weight: 900;
           
          
       }
       p{
              font-size:5rem;
       }
     
      
    }
    
    .right{
        width: 50%;
        border-radius: 50%;
        margin-left: 2rem;
        font-size: 100px;
    }
   
      @keyframes centers {
          0% {
              transform: translateX(-100%);
              text-spacing: 0;
              writing-mode: horizontal-tb; 
             
             
              
          }
            100% {
                transform: translateX(-10%);
                text-spacing: 100%;
                writing-mode: vertical-rl;
                
                
                
            }
      }
      @keyframes imgs {
          0%{
              
              border: 10px solid red;
          }
          33%{
            
              border: 10px solid yellow;
          }
            66%{
                
                border: 10px solid green;
            }
            100%{
               
                border: 10px solid red;
            }


          
      }
      @keyframes txt {
        0%{
            
            color: red;
        }
        33%{
          
            color: yellow;
        }
          66%{
              
              color: green;
          }
          100%{
             
              color:  red;
          }


        
    }

}