.Footer{
    align-items: center;
    justify-content: center;
    display: flex;
    height:50vh;
    flex-direction: column;
    margin-bottom:10px;
    border-top: 1px solid gray;
    width: 100%;
    background-color: greenyellow;
}
.heading{
    font-size:25px;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    margin-bottom:20px;
}
.logos{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content:space-between;
    width: 30%;
    margin: auto;

}
.Logo{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin: auto;
    font-size: 100px;
    
}
.Logo:hover{
    cursor: pointer;
    transform: scale(1.1);
}